  background-color: transparent;
  background-image: linear-gradient(rgba(0, 0, 0, 0.1333333333) 2px, transparent 2px), linear-gradient(90deg, rgba(0, 0, 0, 0.1333333333) 2px, transparent 2px), linear-gradient(rgba(0, 0, 0, 0.1333333333) 1px, transparent 1px), linear-gradient(90deg, rgba(0, 0, 0, 0.1333333333) 1px, rgba(0, 0, 0, 0) 1px);
  background-position: -2px -2px, -2px -2px, -1px -1px, -1px -1px;
  background-size: 250px 250px, 250px 250px, 50px 50px, 50px 50px;
  border-radius: 0px;
}
.letstalk .letstalk__link_layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 0rem;
  height: 0rem;
  border-radius: 50%;
  background-color: #fff;
  transform: translate(-50%, -50%);
