.matrix {
  position: fixed;
  width: 97%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  opacity: 0.5;
  pointer-events: none;
}
.matrix p {
  line-height: 1;
}
.matrix span {
  display: block;
  width: 0.0625rem;
  height: 2dvh;
  font-size: 1rem;
  color: rgba(155, 255, 155, 0.0666666667);
  text-align: center;
  font-family: "Helvetica Neue", Helvetica, sans-serif;
}

.loading-screen {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 30;
  background-color: var(--colGreen);
  color: #000;
}

.counter {
  position: absolute;
  left: 3.125rem;
  bottom: 3.125rem;
  display: flex;
  height: 6.25rem;
  font-size: 6.25rem;
  line-height: 0.9803921569em;
  clip-path: polygon(0 0, 100% 0, 100% 100px, 0 100px);
  color: var(--colBlack);
}

.counter-1, .counter-2, .counter-3 {
  position: relative;
}

.num1offset1 {
  position: relative;
  right: -1.5625rem;
}

.num1offset2 {
  position: relative;
  right: -0.625rem;
}

.home .eye-animation {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3.125rem;
  z-index: 0;
}
@media (max-width: 61.99875em) {
  .home .eye-animation {
    display: none;
  }
}
.home .eyebrow_container {
  display: flex;
  gap: 3.125rem;
}
.home .eyebrow_container .eye_brow {
  width: 3.125rem;
  height: 0.3125rem;
  border-radius: 10px;
  background-color: #fff;
  margin-bottom: 1.25rem;
}
.home .eyebrow_container .left {
  rotate: -20deg;
  border-bottom-left-radius: 0px;
  border-top-left-radius: 90px;
}
.home .eyebrow_container .right {
  rotate: 20deg;
  border-bottom-right-radius: 0px;
  border-top-right-radius: 90px;
}
.home .eye_container {
  gap: 10px;
  display: flex;
}
.home .eye_container .eye {
  position: relative;
  display: inline-block;
  border-radius: 50%;
  height: 60px;
  width: 60px;
  background: #fff;
  box-shadow: inset 0 0 0.625rem rgb(0, 0, 0);
  transition: scale 0.2s, background-color 0.5s, box-shadow 0.5s;
}
.home .eye_container .eye:hover {
  background-color: #ff0000;
  scale: 1.1;
}
.home .eye_container .eye:active {
  scale: 0.8;
}
.home .eye_container .eye::after {
  content: " ";
  position: absolute;
  bottom: 5%;
  right: 50%;
  width: 1.25rem;
  height: 1.25rem;
  background: #000;
  border-radius: 50%;
  border: 0.3125rem solid #2e536f;
  pointer-events: none;
  transition: 0.5s;
}

.cursor._active::after {
  width: 6.25rem;
  height: 6.25rem;
  border-color: #000;
  background-color: #000;
}
.cursor._active svg {
  opacity: 1;
  visibility: visible;
  font-size: 3.125rem;
}
.cursor svg {
  opacity: 0;
  visibility: hidden;
  z-index: 1;
  transform: translate(-50%, -50%);
}
@media (min-width: 61.99875em) {
  .cursor {
    position: fixed;
    top: 0;
    left: 0;
    will-change: transform;
    z-index: 20;
    pointer-events: none;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .cursor::after {
    content: "";
    width: 36px;
    height: 36px;
    top: 0;
    left: 0;
    border: 2px solid var(--colGreen);
    box-shadow: 0 0 22px rgba(255, 255, 255, 0.6);
    position: absolute;
    border-radius: 50%;
    pointer-events: none;
    transition: all 0.3s ease;
    transform: translate(-50%, -50%);
  }
}

