body, html {
  cursor: url("../img/arrowPoint.svg") 7 7, move;
}

body {
  overflow: hidden;
}

mark {
  color: var(--colGreen);
  background-color: transparent;
  font-family: "Cyborg";
}
mark .styles-module_blinkingCursor__yugAC {
  color: #fff !important;
}

::selection {
  background-color: var(--colGreen);
  color: #000;
}

#particles-js {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 10;
}

::-webkit-scrollbar {
  width: 0;
}

.parallax-container {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 30;
}

.header .burger-button-container {
  position: absolute;
  top: 3.125rem;
  right: 3.125rem;
}

.burger-button {
  display: block;
  position: relative;
  flex: 0 0 3.125rem;
  width: 3.125rem;
  height: 1.875rem;
  transition: all 0.3s;
  z-index: 21;
}
.burger-button span, .burger-button::before, .burger-button::after {
  content: "";
  transition: all 0.3s ease 0s;
  right: 0;
  position: absolute;
  height: 0.25rem;
  border-radius: 0.625rem;
  background-color: var(--colGreen);
}
.burger-button::before {
  top: 0;
  width: 100%;
}
.burger-button::after {
  bottom: 0;
  width: 50%;
}
.burger-button span {
  top: calc(50% - 0.0625rem);
  width: 75%;
}

.burger-button._active {
  transform: translateY(0);
}
.burger-button._active span {
  width: 0;
}
.burger-button._active::before {
  top: calc(50% - -0.03125rem);
  transform: rotate(-45deg) translate(1px);
  width: 100%;
}
.burger-button._active::after {
  bottom: calc(50% - 0.0625rem);
  transform: rotate(45deg) translate(1px);
  width: 100%;
}

.burger-body {
  position: fixed;
  top: 12.5rem;
  right: 0;
  transform: translateX(0%);
  width: 43.75rem;
  height: 37.5rem;
  background-color: #878787;
  transition: all 0.3s ease 0s;
  z-index: 20;
}
.burger-body ._active {
  transform: translateX(100%);
}

.home {
  background-color: #000;
  min-height: 100dvh;
  transition: all 1s ease;
}
.home .home-layer {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-image: linear-gradient(rgba(238, 238, 238, 0.0666666667) 2px, transparent 2px), linear-gradient(90deg, rgba(238, 238, 238, 0.0666666667) 2px, transparent 2px), linear-gradient(rgba(238, 238, 238, 0.0666666667) 1px, transparent 1px), linear-gradient(90deg, rgba(238, 238, 238, 0.0666666667) 1px, rgba(0, 0, 0, 0) 1px);
  background-position: -2px -2px, -2px -2px, -1px -1px, -1px -1px;
  background-size: 250px 250px, 250px 250px, 50px 50px, 50px 50px;
  border-radius: 0px;
  opacity: 0.5;
}
.home.link-active {
  background-color: var(--colGreen);
  transition: all 1s ease;
}
.home.link-active * {
  cursor: none;
}
.home.link-active p, .home.link-active span, .home.link-active mark, .home.link-active h1, .home.link-active h2 {
  color: #000;
}
.home.link-active .home-layer {
  background-image: linear-gradient(rgba(0, 0, 0, 0.1333333333) 2px, transparent 2px), linear-gradient(90deg, rgba(0, 0, 0, 0.1333333333) 2px, transparent 2px), linear-gradient(rgba(0, 0, 0, 0.1333333333) 1px, transparent 1px), linear-gradient(90deg, rgba(0, 0, 0, 0.1333333333) 1px, rgba(0, 0, 0, 0) 1px);
  background-position: -2px -2px, -2px -2px, -1px -1px, -1px -1px;
  background-size: 250px 250px, 250px 250px, 50px 50px, 50px 50px;
  border-radius: 0px;
}
.home.link-active .matrix {
  opacity: 0;
}
.home__container {
  display: flex;
  gap: 1.25rem;
  padding-top: 3.125rem;
}
.home__column {
  flex: 1 1 calc(50% - 8.75rem);
}
.home__column:nth-child(2) {
  flex: 1 1 12.5rem;
}
.home__row-head-left {
  margin-top: 18.75rem;
}
.home .col-left, .home .col-right {
  position: relative;
  z-index: 2;
}

.head__content {
  position: relative;
  z-index: 2;
  max-width: 50rem;
  display: flex;
  flex-direction: column;
}
.head__title {
  display: inline-block;
  background: rgba(213, 255, 99, 0.1764705882);
  border-radius: 12px;
  color: var(--colGreen);
  font-size: 1.4rem;
  font-weight: 900;
  letter-spacing: 0.05em;
  padding: 0.5rem 1rem;
  text-transform: capitalize;
  font-size: 1.875rem;
  margin-bottom: 1.25rem;
}
.head__main-text {
  font-size: 3.75rem;
  font-weight: 900;
}
.head__sub-text {
  font-size: 1.25rem;
  line-height: 1.2em;
  opacity: 0.7;
}

.lets-talk {
  width: 100%;
  height: 100dvh;
  position: relative;
  z-index: 1;
}
.lets-talk__container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.lets-talk .lets-talk__link {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 6.25rem;
  border-radius: 6.25rem;
  width: 31.25rem;
  height: 9.375rem;
  background-color: var(--colGreen);
  color: #000;
}

.lets-talk-link {
  position: relative;
  overflow: hidden;
}
.lets-talk-link__text {
  transform: translateY(0.625rem);
}
.lets-talk-link__ripple {
  position: absolute;
  top: var(--y);
  left: var(--x);
  width: 0rem;
  height: 0rem;
  border-radius: 50%;
  background-color: #fff;
  transition: width 0.5s, height 0.5s;
  transform: translate(-50%, -50%);
}
@media (any-hover: hover) {
  .lets-talk-link:hover .lets-talk-link__ripple {
    width: 68.75rem;
    height: 68.75rem;
  }
}

.works {
  padding-top: 12.5rem;
}
.works__container {
  display: flex;
  flex-direction: column;
}
.works__row {
  display: flex;
  gap: 15rem;
  z-index: 1;
}
.works__row:nth-child(even) {
  flex-direction: row-reverse;
}
.works__row:nth-child(even) .works__image {
  transform: perspective(1000px) rotateY(-30deg);
}
.works__row:nth-child(odd) .works__image {
  transform: perspective(1000px) rotateY(30deg);
}
.works__column {
  flex: 1 1 calc(50% - 7.5rem);
  height: 31.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.works__content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.works__image {
  width: 80%;
  height: 60%;
  perspective: 62.5rem;
  transform-style: preserve-3d;
}
.works__title {
  display: inline-block;
  border: 3px solid #14e956;
  box-shadow: 6px 6px 0 0 #14e956;
  color: #14e956;
  font-weight: 900;
  margin-bottom: 3rem;
  padding: 0.7rem 1.1rem;
  text-transform: uppercase;
  font-size: 2.5rem;
  text-align: center;
}
.works__text {
  font-size: 18px;
  margin: 0 0 1.875rem 0;
  text-align: center;
}
.works__link {
  display: inline-block;
  background-color: #14e956;
  background-image: linear-gradient(315deg, #14e956, #72f29a);
  border-radius: 0.4375rem;
  box-shadow: 9px 7px 32px -11px rgba(20, 233, 86, 0.74);
  cursor: pointer;
  font-size: 1.1rem;
  font-weight: 900;
  padding: 0.5rem 0.9rem;
  position: relative;
  text-transform: lowercase;
  transition: all 0.3s ease-in-out 0s;
  color: #000;
  font-size: 1.875rem;
}
@media (any-hover: hover) {
  .works__link:hover::before {
    opacity: 1;
  }
}
.works__link::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  border: 3px solid #72f29a;
  border-radius: 7px;
  box-shadow: 0 0 60px rgba(20, 233, 86, 0.64);
  content: "";
  left: 50%;
  padding: 2.1875rem 3.75rem;
  opacity: 0;
  position: absolute;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  transition: all 0.3s ease-in-out 0s;
}